
import { getName } from '../utils/storageUtils'

export default {
  name: 'Greeting',
  data() {
    return {
      msg: 'Hello'
    }
  },
  computed: {
    getName,
    getGreetingMsg() {
      const hours = new Date().getHours()
      if (hours < 12) {
        return 'Good Morning'
      }
      return hours < 18 ? 'Good Evening' : 'Good Night'
    }
  },
}
