
import { defineComponent } from "vue"


export default defineComponent({
  name: 'Input',
  props: {
    value: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      newValue: this.value,
    };
  },
  methods: {
    onKeypressEnter () {
      this.$emit('key-enter', this.newValue);
    }
  }
});
